import Helper from '@/function.js';
import Md5Unit from '@/pages/utils/md5.js'	
export default {

	data() {
		return {
			token: "",
			oldPass:"",
			newPass:"",
			newPass_2:""
		}
	},
	onLoad: function() {
		this.token = uni.getStorageSync('token');
	},
	methods: {
		
		sendData(){
			var _this = this;
			if (Helper.isEmpty(this.oldPass) || this.oldPass.length < 6)
			{
				uni.showToast({
					title:"请输入6位以上原密码",
					icon:"none"
				})
				
				return;
			}
			
			if (Helper.isEmpty(this.newPass) || this.newPass.length < 6)
			{
				uni.showToast({
					title:"请输入6位以上新密码",
					icon:"none"
				})
				
				return;
			}
			
			if (this.newPass != this.newPass_2)
			{
				uni.showToast({
					title:"两次密码输入不一致",
					icon:"none"
				})
				
				return;
			}
			
			var oldMd5 = Md5Unit.hex_md5(this.oldPass);
			var newMd5 = Md5Unit.hex_md5(this.newPass);
			
			var params={
				oldPass:oldMd5,
				newPass:newMd5
			}
			
			uni.request({
				url: _this.ApiGateWay + '/sale/Member/EditMemberPass',
				data: params,
				method: 'POST',
				dataType: 'json',
				header: {
					Authorization: _this.token
				},
				success: function(res) {
					console.log(res);
						
					if (Helper.CheckData(res)) {
						uni.showModal({
							title:"信息提示",
							content:"修改成功",
							showCancel:false,
							success:function(res){
								uni.navigateBack({
									delta:1
								})
							}
						})		
					} else {
						uni.showModal({
							title:"出错了",
							content:res.data.errorMessage,
							showCancel:false,
							
						})		
					}
				},
				fail: function() {
					//uni.hideLoading();
				},
				complete: function() {
					//uni.hideLoading();
				}
						
						
			})
		}

		

	}

}
